@use "../../assets/variables.scss"as *;
@use "../../assets/mixins.scss"as *;

.candy {
  height: 110vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  @include mobile {
    height: 140vh;
  }
  @include tablet {
    height: 140vh;
  }

  &__body {
    max-width: 80vw;
    height: 60vh;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    background-color: rgba(255, 255, 255, .5);
    border-radius: 20px;
    text-align: center;

    @include tablet {
      width: 80vw;
      height: 120vh;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
    }

    @include mobile {
      width: 70vw;
      min-height: 100vh;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
    }
  }

  &__blurb {
    font-family: $subHeader;
    margin-right: 20px;
    width: 60%;
    color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;



    @include mobile {
      font-size: 3vw;
      margin-right: 0px;
      width: 80%;
      
    }

  }

  &__text {
    font-family: $subHeader;
   
    font-size:1.5vw;

    @include mobile {
      margin-bottom: 3vh;
      font-size:4.5vw;
      
    }
    @include tablet {
      margin-bottom: 3vh;
      font-size:3vw;
      
      
    }

  }

  &__text--header {
    font-size: 2vw;
    font-weight: 800;
    text-decoration: underline;
    
  }
&__text--link {
  margin: 2 0 0 0px;
  font-family: $subHeader;
   
  font-size:1.5vw;

  @include mobile {
    margin-bottom: 3vh;
    font-size:4.5vw;
    
  }
  @include tablet {
    margin-bottom: 3vh;
    font-size:3vw;
    
    
  }
}
  &__text--link:visited {
    text-decoration: none;
    color: inherit;
  }
  &__links-container{
    width: 60vw;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    
  }

  &__skill-images {
    display: flex;
    width: 40vw;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }

  &__skill-image {
    height: 5vh;
   

    @include mobile {
      display: block;
      height: 3vh;
    }
  }


  &__header {
    font-family: $header;
    font-size: 5vw;
    width: 50vw;
    text-align: center;
    color: white;
    font-weight: 100;
    margin-top: 5px;
    margin-bottom: 25px;

    @include mobile {
      font-size: 10vw;
      margin-top: 3vh;
    }
  }

  &__image {
    margin: 2vh;
    height: 70%;
    border-radius: 10px;

    @include mobile {
      height: 20vh;

    }
      @include tablet {
        max-height: 30vh;
      }
  
    
}

  &__button {
    margin-top: 4vh;
    box-shadow: inset 0px 1px 0px 0px #fff6af;
    background: linear-gradient(to bottom, #ffec64 5%, #ffab23 100%);
    background-color: #ffec64;
    border-radius: 6px;
    border: 1px solid #ffaa22;
    display: inline-block;
    cursor: pointer;
    color: #333333;
    font-family: Arial;
    font-size: 15px;
    font-weight: bold;
    padding: 6px 24px;
    text-decoration: none;
    text-shadow: 0px 1px 0px #ffee66;

    @include mobile {
      margin-bottom: 3vh;

  }
  }

  &__button:hover {
    background: linear-gradient(to bottom, #ffab23 5%, #ffec64 100%);
    background-color: #ffab23;
  }

  &__button:active {
    position: relative;
    top: 1px;
  }
}